@import url('https://fonts.googleapis.com/css2?family=Koulen&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Macondo&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Allura&family=Merriweather:wght@700&family=Roboto+Slab:wght@600&display=swap');
.App {
  text-align: center;
  background-color: aliceblue;
  min-height: 100vh;
  font-family: 'Koulen', cursive;
}
.navbar{padding-left: 7vw;}
.navbar-brand{font-family: 'Allura', cursive;font-size: 4vw;}
.toggle-btn{
  position: absolute;
  right: 7vw;
  font-size: 1.5em;
}
.settings, .choose-sound{
  position: absolute;
  background-color: #fff;
  border-radius: 9px;
  padding: 5vh 5vw; 
  margin: auto; left: 0; right: 0; top: 0; bottom: 0;
  height: 30rem;
  width: 35%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
h1{ font-size: 2.5vw;}
@media (max-width:570px) {
  .settings, .choose-sound{margin: 12vh 5vw; width: 90%;}
  h1{font-size: 7vw;}
  .navbar-brand{font-size: 9vw;}
}



.timing-input > .mb-3{margin-bottom: 0 !important;}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; 
}

input[type=number] {
    -moz-appearance:textfield;
}
.settings input,  button{color: #49D4B7 !important;}
input{
  background-color: aliceblue !important;
  border-radius: 2rem !important;
  border-width: 0px !important;
  text-align: center;
}
.increaseBtn{
  background: transparent ;
  border: none;
  position: relative;
  left: 1.8em;
  top:calc(-1em - 0.75rem - 2px);
}
.decreaseBtn{
  background: transparent ;
  border: none;
  position: relative;
  left: -3.5em;
  top:calc(1.65em + 0.75rem + 2px);
}
.decreaseBtn:hover, .increaseBtn:hover{
  background-color: #c5fcf0;
  border-radius: 50%;
}
.repeat-times >.increaseBtn{left: 6em;}
.repeat-times >.decreaseBtn{left: -8.5em;}
.btn{width: 100%; }
.add-sound-btn{
  background-color: aliceblue;
  height: (1.6em + 0.75rem + 2px);
  margin-bottom: 2em;
  border-radius: 2rem !important;
}
.btn-primary{
  border: none;
  border-radius: 2rem !important;
  background-color: #49D4B7;
  color: #fff !important;
}
.btn-primary:hover{background-color: #0f5052;}

.square{
  padding-bottom: 35%;
  background-size: cover;
  background-position: center;
  margin: 2vw;
  box-shadow: 3px 4px 9px -3px rgba(0,0,0,0.54);
  -webkit-box-shadow: 3px 4px 9px -3px rgba(0,0,0,0.54);
  -moz-box-shadow: 3px 4px 9px -3px rgba(0,0,0,0.54);
}
.square:hover{
  transform: scale(1.1);
  transition-duration: 0.8s;
}
.square span{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.705);
}

.time-card{
  background-color: #fff;
  color: #0f5052;
  font-family: 'Roboto Slab', serif;
  font-size: 7vw;
  padding: 3vw;
  margin: 2vw;
  width: 3em;
  text-align: center;
  line-height: 2em;
  height: 3em;
  box-shadow: 3px 6px 6px 0px rgba(73,212,185,0.4);
  -webkit-box-shadow: 3px 6px 6px 0px rgba(73,212,185,0.4);
  -moz-box-shadow: 3px 6px 6px 0px rgba(73,212,185,0.4);
  border-radius: 9px;
}
.time-card span{ font-size: 11vw; }
.cards-div{
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: fit-content;
  
}
.two-dots{
  margin: 2vw;
  padding-top: 3vw;
}
.dot{
  height: 3vw; width: 3vw;
  margin: 3vw 0.5vw;
  background-color: #0f5052;
  border-radius: 4px;
  
}

.congratswrap{
  width: 70vw; height: 70vh;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding-top: 17vh;
  border-radius: 9px;
}
.congratswrap button{
  display: block;
  position: relative;
  margin: 2vw auto;
  background-color: #49D4B7; color: #fff !important;
  font-size: larger;
  padding: 1.5vh 1vw;
  border: none;
  border-radius: 5px;
}
.congratswrap img{
  display: inline;
  position: relative;
  bottom: 0;
  transform: rotate(90deg);
  animation: rotate-star 0.5s ease infinite ;
  margin: 1em;
}

.applause img:nth-child(2){
  animation-delay: 0.1s;
  position: relative;
}

@keyframes rotate-star {
  0%{ transform:  rotate(0deg);}
  50%{ transform: rotate(20deg) scale(1.1);}
  100%{ transform: rotate(0deg);}
}